import vue from 'vue'
import vuex from 'vuex'

vue.use(vuex)

const state = {
    comVI: {
        title: '',
        ico: '',
        logo: ''
    }
}

const mutations = {
    setComVI(state, data) {
        state.comVI = data;
    }
}

const action = {
    commitComVI: ({ commit }, comVI) => commit('setComVI', comVI)
}

export default new vuex.Store({
    state,
    mutations
})