<template>
  <div>
    <el-card v-loading="diaLoading">
      <el-row justify="space-between" type="flex" :gutter="20" class="oneRow">
        <el-col :span="24">
          <div class="g_label">
            <div v-if="this.configGd.isAdmin" class="div_inline">
              :
              <el-input
                size="small"
                clearable
                class="el-input_co"
                style="width:150px"
                placeholder=""
                v-model="seekCompanyName"
                @keyup.enter.native="getList"
                @clear="getList"
              />
            </div>
            <div class="g_label div_inline">
              <!--  -->
              <el-button
                @click="getList"
                class="el-button_co"
                type="primary"
                icon="el-icon-search"
                :loading="diaLoading"
              ></el-button>
            </div>
            <div class="g_label div_inline div_right">: {{pageTotal}}</div>
          </div>
        </el-col>
      </el-row>
      <el-row justify="space-between" type="flex" :gutter="20" class="twoRow">
        <el-col :span="24">
          <el-button-group>
            <!--  -->
            <el-button
              @click="toAddData"
              class="el-button_co"
              type="primary"
              icon="el-icon-circle-plus-outline"
            ></el-button>
            <!--  -->
            <el-button
              @click="doDel"
              class="el-button_co"
              type="danger"
              icon="el-icon-remove-outline"
            ></el-button>
            <!--  -->
            <!--  -->
            <el-button class="el-button_co" type="primary" icon="el-icon-upload2"></el-button>
            <!--  -->
            <el-button class="el-button_co" type="primary" icon="el-icon-download"></el-button>
          </el-button-group>
        </el-col>
      </el-row>

      <el-row>
        <!--  -->
        <el-table
          style="width: 100%"
          :header-cell-style="{background:'#eee',color:'#000'}"
          :data="listdata"
          @selection-change="handleSelectionChange"
          stripe
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="company_id" label="" align="center" width="60px"></el-table-column>
          <el-table-column prop="company_name" label="" align="center"></el-table-column>
          <el-table-column prop="company_address" label="" align="center"></el-table-column>
          <el-table-column prop="company_tel" label="" align="center"></el-table-column>
          <el-table-column label="" align="center">
            <template scope="scope">
              {{ scope.row.company_time.substr(0, 16) }}
            </template>
          </el-table-column>
          <el-table-column label="" align="center">
            <template slot-scope="scope">
              <el-button-group>
                <el-button size="mini" icon="el-icon-edit" @click="toEditData(scope)"></el-button>
                <el-button size="mini" icon="el-icon-delete" @click="doDel(scope)"></el-button>
              </el-button-group>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
      <!--  -->
      <el-row>
        <div class="page_div">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="pageTotal"
          ></el-pagination>
        </div>
      </el-row>
    </el-card>

    <!--  -->
    <el-dialog
      :close-on-click-modal="false"
      :title="adTitle"
      :visible.sync="dialogVisible"
      :destroy-on-close="true"
      width="590px"
    >
      <el-form
        v-loading="diaLoading"
        :validate-on-rule-change="false"
        :model="ruleForm"
        :rules="rules()"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-row style="margin-bottom: -15px">
          <el-col :span="12">
            <el-form-item label="ID" prop="company_id" autocomplete="off" v-show="false">
              <el-input v-model="ruleForm.company_id"></el-input>
            </el-form-item>
            <el-form-item label="" prop="company_name" autocomplete="off">
              <input
                name="us"
                type="text"
                placeholder=""
                style="position:absolute;left: -9999999px;"
              />
              <el-input
                clearable
                size="small"
                placeholder=""
                v-model="ruleForm.company_name"
                :maxlength="20"
                :readonly="readOnlyFlag"
                @focus="readOnlyFlag = false;"
                autocomplete="new-accounts"
                style="width:220px;"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="company_tel" autocomplete="off">
              <el-input
                clearable
                size="small"
                placeholder=""
                v-model="ruleForm.company_tel"
                :maxlength="20"
                :readonly="readOnlyFlag"
                @focus="readOnlyFlag = false;"
                autocomplete="new-accounts"
                style="width:220px;"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="company_address" autocomplete="off">
              <el-input
                clearable
                size="small"
                placeholder=""
                v-model="ruleForm.company_address"
                :maxlength="20"
                :readonly="readOnlyFlag"
                @focus="readOnlyFlag = false;"
                autocomplete="new-accounts"
                style="width:420px;"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-divider></el-divider>
        <el-form-item style="padding-left: 90px; margin: -10px 0 5px 0">
          <el-button
            ref="submitBut"
            type="primary"
            @click="insert('ruleForm')"
            size="small"
            icon="el-icon-circle-check"
            :loading="diaLoading"
          >{{butTitleSubmit}}</el-button>
          <el-button @click="resetForm('ruleForm')" size="small" icon="el-icon-refresh-left"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dialogVisible: false,
      diaLoading: false,
      // 
      ruleForm: {
        company_id: 0,
        company_name: '',
        company_address: '',
        company_tel: ''
      },
      // 
      editCompanyData: {
        company_id: 0,
        company_name: '',
        company_address: '',
        company_tel: ''
      },
      // 
      editKeyList: ['company_name', 'company_address', 'company_tel'],
      // 
      rulesBas: {
        company_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        company_tel: [
          // validator,(checkName) callback 
          {
            required: true,
            trigger: 'blur',
            validator: this.checkPhone
          }
        ]
      },
      readOnlyFlag: true,
      butTitleSubmit: '',
      delId: [],
      delNames: [],
      adTitle: '', // ()
      /** 123 */
      isATyle: 0,

      listdata: [],
      // 
      seekCompanyName: '',
      currentPage: 1,
      pageSize: 13,
      pageTotal: 0
    }
  },
  created() {
    this.getList()
  },
  methods: {
    rules() {
      var myReRules = JSON.parse(JSON.stringify(this.rulesBas))
      myReRules.company_tel[0].validator = this.checkPhone
      return myReRules
    },
    // 
    intSimData() {
      this.ruleForm = {
        company_id: 0,
        company_name: '',
        company_address: '',
        company_tel: ''
      }
      if (this.isATyle === 2) {
        for (var I in this.editCompanyData) {
          this.ruleForm[I] = this.editCompanyData[I]
        }
      }
    },
    // 
    udShowStyle(_start, _noInt) {
      this.diaLoading = _start
      if (_start) {
      } else {
        if (_noInt) {
        } else {
          this.intSimData()
        }
      }
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.intSimData()
    },
    // 
    insert(formName) {
      // var _this = this
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.apiSaveUser()
          this.udShowStyle(true)
        } else {
          return false
        }
      })
    },
    // 
    urResponseErr(_errmsg) {
      if (this.isATyle !== 3) {
        // this.$refs.upload.abort()
        // this.$refs.upload.clearFiles()
        // this.removeFile()
      }
      this.udShowStyle(false, true)
      this.$message.error(_errmsg)
    },

    // -
    toAddData() {
      this.isATyle = 1
      this.adTitle = ''
      this.intSimData()
      this.udShowStyle(false)
      this.butTitleSubmit = ''
      this.dialogVisible = true
    },

    // -
    toEditData(scope) {
      this.isATyle = 2
      this.adTitle = ''
      this.intSimData()
      for (var I in scope.row) {
        this.editCompanyData[I] = scope.row[I]
      }
      this.udShowStyle(false)
      this.butTitleSubmit = ''
      this.dialogVisible = true
    },
    // 
    async doDel(scope) {
      this.isATyle = 3
      if (scope && scope.row) {
      } else if (!this.delId.length) {
        this.$message({ message: '!', type: 'warning' })
        return false
      }
      var myDelIDs = []
      var myNames = ''
      if (scope && scope.row) {
        // this.delId.push(scope.row.company_id)
        // this.delNames.push(scope.row.company_name)
        // this.appDelList(scope.row)
        myNames = scope.row.company_name
        myDelIDs.push(scope.row.company_id)
      } else {
        for (var i = 0; i < this.delNames.length; i++) {
          if (i < 3) {
            myNames += this.delNames[i] + ','
            if (i >= 2 && this.delNames.length > 3) {
              myNames += '...'
            }
          }
          myDelIDs.push(this.delId[i])
        }
        if (myNames.charAt(myNames.length - 1) === ',') {
          myNames = myNames.substr(0, myNames.length - 1)
        }
      }
      var myNumsTip = ''
      if (myDelIDs.length > 1) {
        myNumsTip = '  ' + myDelIDs.length + ' '
      }
      const confirmResult = await this.$confirm(
        ' [' + myNames + ']' + myNumsTip + ' ?',
        '',
        {
          cancelButtonText: '',
          confirmButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      this.udShowStyle(true)
      this.$http
        .post('/api' + '/company/del', myDelIDs)
        .then(res => {
          if (res.data.code === 0) {
            this.dialogVisible = false
            this.$message({
              message: '',
              type: 'success'
            })
            this.udShowStyle(false, true)
            this.getList()
          } else {
            this.urResponseErr('' + res.data.message)
          }
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          this.urResponseErr('' + err)
        })
    },
    // 
    apiSaveUser() {
      this.udShowStyle(true)
      var config = null
      var formData = null
      if (this.isATyle === 1) {
        formData = this.ruleForm
      } else if (this.isATyle === 2) {
        config = {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }
        formData = new FormData()
        if (this.isATyle === 2) {
          formData.append('company_id', this.ruleForm.company_id)
        }
        for (let i = 0; i < this.editKeyList.length; i++) {
          formData.append(this.editKeyList[i], this.ruleForm[this.editKeyList[i]])
        }
        // for (var I in this.ruleForm) {
        //   formData.append(I, this.ruleForm[I])
        // }
      }
      this.$http
        // .post(this.uploadUrl(), this.ruleForm)
        .post(this.uploadUrl(), formData, config)
        .then(res => {
          if (res) {
            if (res.data.code === 0) {
              this.dialogVisible = false
              this.$message({
                message: this.adTitle + '',
                type: 'success'
              })
              this.getList()
            } else {
              this.urResponseErr(this.adTitle + '' + res.data.message)
            }
          } else {
            this.urResponseErr(this.adTitle + '')
          }
          // this.udShowStyle(false, true)
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          this.urResponseErr(this.adTitle + ':' + err)
          // this.udShowStyle(false, true)
        })
      // eslint-disable-next-line no-unreachable
      this.udShowStyle(false, true)
    },
    // api
    uploadUrl: function() {
      if (this.isATyle === 1) {
        return '/api' + '/company/add'
      } else if (this.isATyle === 2) {
        return '/api' + '/company/update'
      }
    },
    // 
    handleSelectionChange(val) {
      this.delId = []
      this.delNames = []
      val.forEach((ele, indx) => {
        this.delId.push(ele.company_id)
        this.delNames.push(ele.company_name)
      })
    },
    checkPhone(rule, value, callback) {
      if (!value) {
        return callback(new Error(''))
      }
      // const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
      const phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
      const phoneReg2 = /^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/
      if (phoneReg.test(value) || phoneReg2.test(value)) {
        callback()
      } else {
        return callback(new Error(''))
      }
    },

    // 
    getList() {
      var apiUrl =
        '/api' +
        '/company/find/list' +
        '?page=' +
        this.currentPage +
        '&pageSize=' +
        this.pageSize
      if (this.seekCompanyName) {
        apiUrl += '&company_name=' + this.seekCompanyName
      }
      if (this.seekCompanyAddress) {
        apiUrl += '&company_address=' + this.seekCompanyAddress
      }
      this.udShowStyle(true)
      this.$http
        .get(apiUrl)
        .then(res => {
          if (res.data.code === 0 && res.data.data) {
            this.listdata = res.data.data.list
            this.pageTotal = res.data.data.total
            this.udShowStyle(false)
          } else {
            this.urResponseErr('' + res.data.message)
          }
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          this.urResponseErr('' + err)
        })
    },
    // 
    handleSizeChange(val) {
      // console.log(` ${val} `)
    },
    handleCurrentChange(val) {
      // console.log(`: ${val}`)
      this.currentPage = val
      this.getList(this.currentPage)
    }
  }
}
</script>
<style lang="less">

</style>
